import { test } from 'blue-tape';
import path from 'path';
import run from '../../../../test/helpers';

const bin = path.resolve(__dirname, '../../bin/bb-generate.js');

test('e2e :: bb-generate :: main command', assert => {
  const cmd = [bin];
  return run(cmd).then(res => {
    const actual = res.stdout.includes('NAME: bb-generate item');
    const expected = true;
    assert.equal(actual, expected, 'Should display the help.');
    return res;
  }).then(res => {
    const actual = res.code;
    const expected = 1;
    assert.equal(actual, expected, 'Should exit with partial success exit code');
    return res;
  }).then(res => {
    const actual = res.stderr.includes('Missing required <name>');
    const expected = true;
    assert.equal(actual, expected, 'Should display the error info');
    return res;
  });
});

